import {changeProfileStatusThunkCreator} from "../../../../store/reducers/profileReducer";
import {connect} from "react-redux";
import {ProfileStatus} from "./ProfileStatus";

const mapDispatchToProps = dispatch => {
    return {
        changeStatus: formData => {
            dispatch(changeProfileStatusThunkCreator(formData));
        }
    };
};

export const ProfileStatusContainer = connect(null, mapDispatchToProps)(ProfileStatus)