import {followOnUserDAL, getUsersDAL, unFollowOnUserDAL} from "../../api/api";
import {setIsOpeningModalActionCreator} from "./modalReducer";

const getUsers                          = "users/GET USERS";
const setIsFetching                     = "users/SET IS FETCHING";
const setIsFetchingForChangePageUser    = "users/SET IS FETCHING FOR PAGE USER";

const usersInit = {
    usersList: null,
    pageSize: 5,
    page: 1,
    totalUserCount: null,
    isFetching: false,
    isFetchingForChangePageUser: false
};

export const usersReducer = (state = usersInit, action) => {
    const newState = {...state};

    switch (action.type) {
        case getUsers:
            newState.usersList      = [...action.usersList];
            newState.totalUserCount = action.totalUserCount;
            newState.page           = action.page;

            return newState;
        case setIsFetching:
            newState.isFetching = action.isFetching;
            return newState;
        case setIsFetchingForChangePageUser:
            newState.isFetchingForChangePageUser = action.isFetchingForChangePageUser;
            return newState;
        default: return state;
    };
};

const getUsersActionCreator = (usersList, totalUserCount, page) => {
    return {type: getUsers, usersList, totalUserCount, page};
};

const setIsFetchingActionCreatorUsers = isFetching => {
    return {type: setIsFetching, isFetching};
};

const setIsFetchingForChangePageUserActionCreatorUsers = isFetchingForChangePageUser => {
    return {type: setIsFetchingForChangePageUser, isFetchingForChangePageUser};
};

export const getUsersThunkCreator = page => async dispatch => {
   try {
       dispatch(setIsFetchingForChangePageUserActionCreatorUsers(true));

       const response = await getUsersDAL(page);

       dispatch(getUsersActionCreator(response.data.items, response.data.totalCount, page));
       dispatch(setIsFetchingForChangePageUserActionCreatorUsers(false));
   } catch (error) {
       dispatch(setIsOpeningModalActionCreator(true));
   };
};

const followUnFollowOnUserUniThunkCreator = (userId, page, DAL) => async dispatch => {
    try {
        dispatch(setIsFetchingActionCreatorUsers(true));

        const response = await DAL(userId);

        if (response.data.resultCode === 0) {
            await dispatch(getUsersThunkCreator(page));
            dispatch(setIsFetchingActionCreatorUsers(false));
        };
    } catch (error) {
        dispatch(setIsOpeningModalActionCreator(true));
    };
};

export const followOnUserThunkCreator = (userId, page) => followUnFollowOnUserUniThunkCreator(userId, page, followOnUserDAL);
export const unFollowOnUserThunkCreator = (userId, page) => followUnFollowOnUserUniThunkCreator(userId, page, unFollowOnUserDAL);