import * as axios from "axios";

const instance = axios.create({
    baseURL: "https://social-network.samuraijs.com/api/1.0",
    withCredentials: true,
    headers: {"API-KEY": "98118e87-f74e-47ed-baba-95942234d67c"}
});

export const logInDAL = formData => {
    return instance.post("/auth/login", formData);
}

export const logOutDAL = () => {
    return instance.delete("/auth/login");
};

export const getCaptchaDAL = () => {
    return instance.get("/security/get-captcha-url");
};

export const getMyProfileDAL = () => {
    return instance.get("/auth/me");
};

export const getProfileDAL = userId => {
    return instance.get(`/profile/${userId}`);
};

export const changeProfileInfoDAL = profile => {
    return instance.put("/profile", profile);
};

export const changeProfileStatusDAL = status => {
    return instance.put("/profile/status", status);
};

export const getStatusUserDAL = userId => {
    return instance.get(`/profile/status/${userId}`);
};

export const changePhotoOnProfileDAL = image => {
    const formData = new FormData();
    formData.append("image", image);

    return instance.put("/profile/photo", formData);
};

export const getUsersDAL = page => {
    return instance.get(`/users?count=5&page=${page}`);
};

export const followOnUserDAL = userId => {
    return instance.post(`/follow/${userId}`, {});
};

export const unFollowOnUserDAL = userId => {
    return instance.delete(`/follow/${userId}`);
};