import React from "react";
import {Button, Image} from "antd";

export const Header = ({isAuth, logOut}) => {
    return (
        <header style={style.header}>
            <div className="wrapper">
                <div style={style.headerWrap}>
                    <Image width={100} src="https://gw.alipayobjects.com/zos/antfincdn/aPkFc8Sj7n/method-draw-image.svg"/>
                    {isAuth ? <Button onClick={logOut}>Log out</Button> : null}
                </div>
            </div>
        </header>
    );
};

const style = {
    header: {
        padding: "20px 0"
    },
    headerWrap: {
        display: "grid",
        justifyContent: "space-between",
        gridTemplateColumns: "100px 100px",
        alignItems: "center"
    }
};