import {connect} from "react-redux";
import {
    getCaptchaThunkCreator,
    logInThunkCreator,
} from "../../store/reducers/logInLogOutReducer";
import {getCaptchaUrl, getIsFetching, getIsNotBotInProcessing} from "../../store/reselect/reselect";
import {Login} from "./Login";

const mapStateToProps = state => {
    return {
        captchaUrl: getCaptchaUrl(state),
        isNotBotInProcessing: getIsNotBotInProcessing(state),
        isFetching: getIsFetching(state)
    };
};

const mapDispatchToProps = dispatch => {
    return {
        logIn: formData => {
            dispatch(logInThunkCreator(formData));
        },
        getCaptcha: () => {
            dispatch(getCaptchaThunkCreator());
        }
    };
};

export const LoginContainer = connect(mapStateToProps, mapDispatchToProps)(Login)