import React from "react";
import {Form, Input} from "antd";
import {Captcha} from "./Captcha/Captcha";
import {LoginBtn} from "./LoginBtn/LoginBtn";

export const Login = ({logIn, getCaptcha, captchaUrl, isNotBotInProcessing, isFetching}) => {
    return (
        <div>
            <Form initialValues={{email: "", password: "", captcha: ""}} onFinish={logIn}>
                <Form.Item name={"email"} label={"Email"} rules={[{required: true}]}>
                    <Input type={"text"}/>
                </Form.Item>
                <Form.Item name={"password"} label={"Password"} rules={[{required: true}]}>
                    <Input type={"password"}/>
                </Form.Item>
                <Captcha captchaUrl={captchaUrl}/>
                <LoginBtn isNotBotInProcessing={isNotBotInProcessing} getCaptcha={getCaptcha}/>
            </Form>
        </div>
    );
};