import {Button, Upload} from "antd";
import React from "react";

export const ProfileButton = ({isEditModeProfileInfo, changeEditModeProfileInfo, changePhoto, yourProfile}) => {
    return (
        <div style={styles.profileBtnWrap}>
            {
                isEditModeProfileInfo === false
                    ? <Button
                        type={"default"}
                        style={styles.btn}
                        size={"large"}
                        onClick={() => changeEditModeProfileInfo(true)}
                    >
                        Change profile info
                    </Button>
                    : null
            }
            <Upload size={"large"} customRequest={() => ""} action={image => changePhoto(image, yourProfile.id)}>
                <Button size={"large"} style={styles.btn}>Upload file</Button>
            </Upload>
        </div>
    );
};

const styles = {
    profileBtnWrap: {
        display: "grid",
        gridTemplateColumns: "repeat(3, 200px)",
        gridTemplateRows: "repeat(1, 60px)"
    },
    btn: {
        margin: "0 20px",
        boxSizing: "content-box",
        height: 30
    }
};