import React from "react";
import {Image, Form, Input} from "antd";

export const Captcha = ({captchaUrl}) => {
    if (captchaUrl) {
        return (
            <>
                <Form.Item name={"captcha"} label={"Captcha"} rules={[{required: true}]}>
                    <Input type={"text"}/>
                </Form.Item>
                <Form.Item>
                    <Image width={200} height={200} src={captchaUrl} alt={captchaUrl}/>
                </Form.Item>
            </>
        );
    };

    return null;
};