import {changeProfileInfoThunkCreator} from "../../../store/reducers/profileReducer";
import {connect} from "react-redux";
import {ProfileFormInfo} from "./ProfileFormInfo";

const mapDispatchToProps = dispatch => {
    return {
        changeProfileInfo: (profile, userId) => {
            dispatch(changeProfileInfoThunkCreator(profile, userId));
        }
    };
};

export const ProfileFormInfoContainer = connect(null, mapDispatchToProps)(ProfileFormInfo)