import React from "react";
import {Button, Form, Input} from "antd";

export const ProfileFormInfo = ({changeEditModeProfileInfo, changeProfileInfo, yourProfile}) => {
    const onFinish = formData => {
        changeEditModeProfileInfo(false);
        changeProfileInfo(formData, yourProfile.id);
    };

    return (
        <Form onFinish={onFinish} initialValues={{fullName: "", aboutMe: "", lookingForAJob: "", lookingForAJobDescription: "", github: "", vk: ""}}>
            <Form.Item name={"fullName"} rules={[{required: true}]} label={"fullName"}>
                <Input size={"large"}/>
            </Form.Item>
            <Form.Item name={"aboutMe"} rules={[{required: true}]} label={"aboutMe"}>
                <Input size={"large"}/>
            </Form.Item>
            <Form.Item name={"lookingForAJob"} rules={[{required: true}]} label={"lookingForAJob"}>
                <Input size={"large"}/>
            </Form.Item>
            <Form.Item name={"lookingForAJobDescription"} rules={[{required: true}]} label={"lookingForAJobDescription"}>
                <Input size={"large"}/>
            </Form.Item>
            <Form.Item name={"github"} rules={[{required: true}]} label={"github"}>
                <Input size={"large"}/>
            </Form.Item>
            <Form.Item name={"vk"} rules={[{required: true}]} label={"vk"}>
                <Input size={"large"}/>
            </Form.Item>
            <Button type={"primary"} htmlType={"submit"} size={"large"}>SAVE</Button>
        </Form>
    );
};