import React, {useEffect, useState} from "react";
import {Image} from "antd";
import {ProfileButton} from "./ProfileButton/ProfileButton";
import {ProfileFormInfoContainer} from "./ProfileFormInfo/ProfileFormInfoContainer";
import {ProfileInfoContainer} from "./ProfileInfo/ProfileInfoContainer";
import {Loading} from "../Loading/Loading";

export const Profile = ({getStatus, getProfile, profile, match, yourProfile, changePhoto, isFetching}) => {
    const [isEditModeProfileInfo, changeEditModeProfileInfo] = useState(false);

    useEffect(() => {
        if (yourProfile && !match.params.userId) {
            getProfile(yourProfile.id);
            getStatus(yourProfile.id);
            return null;
        };

        if (match.params.userId) {
            getProfile(match.params.userId);
            getStatus(match.params.userId);
            return null;
        };
    }, [match]);

    if (isFetching) {
        return <Loading/>;
    };

    return (
        <div style={styles.profileUser}>
            <div>
                <Image
                    width={100}
                    src={
                        profile && profile.photos.small
                            ? profile.photos.small
                            : "https://bit.ly/3sQ23rN"
                    }
                />
            </div>
            {
                isEditModeProfileInfo
                    ? <ProfileFormInfoContainer
                        yourProfile={yourProfile}
                        changeEditModeProfileInfo={changeEditModeProfileInfo}
                    />
                    : <ProfileInfoContainer profile={profile}/>
            }
            {
                yourProfile
                    ? yourProfile.id === match.params.userId || !match.params.userId
                        ? <ProfileButton
                            isEditModeProfileInfo={isEditModeProfileInfo}
                            changeEditModeProfileInfo={changeEditModeProfileInfo}
                            changePhoto={changePhoto}
                            yourProfile={yourProfile}
                        />
                        : null
                    : null
            }
        </div>
    );

};

const styles = {
    profileUser: {
        display: "grid",
        gridTemplateColumns: "100px 400px",
        gridGap: "0 50px"
    }
};