const openModal = "modal/OPEN MODAL";

const modalInit = {
    isOpeningModal: false
};

export const modalReducer = (state = modalInit, action) => {
    const newState = {...state};

    switch (action.type) {
        case openModal:
            newState.isOpeningModal = action.isOpeningModal;
            return newState;
        default: return state;
    };
};

export const setIsOpeningModalActionCreator = (isOpeningModal) => {
    return {type: openModal, isOpeningModal};
};