import {connect} from "react-redux";
import {Profile} from "./Profile";
import {
    changePhotoOnProfileThunkCreator,
    getProfileStatusThunkCreator,
    getProfileThunkCreator
} from "../../store/reducers/profileReducer";
import {compose} from "redux";
import {withRouter} from "react-router-dom";
import {getIsFetchingProfile, getMyProfile, getProfile} from "../../store/reselect/reselect";

const mapStateToProps = state => {
    return {
        profile: getProfile(state),
        yourProfile: getMyProfile(state),
        isFetching: getIsFetchingProfile(state)
    };
};

const mapDispatchToProps = dispatch => {
    return {
        getProfile: userId => {
            dispatch(getProfileThunkCreator(userId));
        },
        getStatus: userId => {
            dispatch(getProfileStatusThunkCreator(userId));
        },
        changePhoto: (image, userId) => {
            dispatch(changePhotoOnProfileThunkCreator(image, userId));
        }
    };
};

export const ProfileContainer = compose(withRouter, connect(mapStateToProps, mapDispatchToProps))(Profile);