import React, {useState} from "react";
import {ProfileStatusContainer} from "./profileStatus/ProfileStatusContainer";

const checkInfo = (parent, info) => {
     return `${info}`;
};

export const ProfileInfo = ({profile, status}) => {
    const [isEditModeStatus, changeEditModeStatus] = useState(false);

    return (
        <div>
            <p style={styles.infoProfile}>Name: {profile ? checkInfo(profile, profile.fullName) : "Неизвестно"}</p>
            <p style={styles.infoProfile}>About me: {profile ? checkInfo(profile, profile.aboutMe) : "Неизвестно"}</p>
            <p style={styles.infoProfile}>Looking for job: {profile ? checkInfo(profile, profile.lookingForAJob) : "Неизвестно"}</p>
            <p style={styles.infoProfile}>About looking job: {profile ? checkInfo(profile, profile.lookingForAJobDescription) : null}</p>
            <p style={styles.infoProfile}>Github: {profile ? checkInfo(profile, profile.contacts.github) : "Неизвестно"}</p>
            <p style={styles.infoProfile}>Vk: {profile ? checkInfo(profile, profile.contacts.vk) : "Неизвестно"}</p>
            {
                isEditModeStatus
                ? <ProfileStatusContainer changeEditModeStatus={changeEditModeStatus}/>
                : <p onClick={() => changeEditModeStatus(true)} style={styles.infoProfile}>Status: {checkInfo(status, status)}</p>
            }
        </div>
    );
};

const styles = {
    infoProfile: {
        paddingBottom: 10,
        fontSize: 25
    }
};