import {getCaptchaDAL, getMyProfileDAL, logInDAL, logOutDAL} from "../../api/api";
import {setIsOpeningModalActionCreator} from "./modalReducer";

const logOut            = "logInLogOut/LOG OUT";
const logIn             = "logInLogOut/LOG IN";
const getMyProfile      = "logInLogOut/GET MY PROFILE";
const setCaptcha        = "logInLogOut/SET CAPTCHA";
const setIsFetching     = "logInLogOut/SET IS FETCHING";

const logInLogOutInit = {
    profile: null,
    isAuth: null,
    captchaUrl: null,
    isNotBotInProcessing: false,
    isFetching: false,
};

export const logInLogOutReducer = (state = logInLogOutInit, action) => {
    const newState = {...state};

    switch (action.type) {
        case logOut:
            newState.profile = null;
            newState.isAuth = false;

            return newState;
        case logIn:
            newState.profile = {...action.profile};
            newState.isAuth  = action.isAuth;

            return newState;
        case getMyProfile:
            newState.profile    = {...action.profile};
            newState.isAuth     = action.isAuth;

            return newState;
        case setCaptcha:
            newState.captchaUrl             = action.url;
            newState.isNotBotInProcessing   = true;

            return newState;
        case setIsFetching:
            newState.isFetching = action.isFetching;
            return newState;
        default: return state;
    }
};

const logOutActionCreator = () => {
    return {type: logOut};
};

export const setIsFetchingLogInLogOutActionCreator = isFetching => {
    return {type: setIsFetching, isFetching};
};

const getMyProfileActionCreator = (isAuth, profile) => {
    return {type: getMyProfile, isAuth, profile};
};

const setUrlCaptchaActionCreator = url => {
    return {type: setCaptcha, url};
};

export const getMyProfileThunkCreator = () => async dispatch => {
    try {
        dispatch(setIsFetchingLogInLogOutActionCreator(true));

        const response = await getMyProfileDAL();

        if (response.data.resultCode === 0) {
            dispatch(getMyProfileActionCreator(true, response.data.data));
            dispatch(setIsFetchingLogInLogOutActionCreator(false));

            return null;
        };

        dispatch(getMyProfileActionCreator(false, response.data.data));
        dispatch(setIsFetchingLogInLogOutActionCreator(false));
    } catch (error) {
        dispatch(setIsOpeningModalActionCreator(true));
    };
};

export const logInThunkCreator = formData => async dispatch => {
    try {
        dispatch(setIsFetchingLogInLogOutActionCreator(true));
        const response = await logInDAL(formData);

        if (response.data.resultCode === 0) {
            await dispatch(getMyProfileThunkCreator());
            dispatch(setIsFetchingLogInLogOutActionCreator(false));
        };
    } catch (error) {
        dispatch(setIsOpeningModalActionCreator(true));
    };
};

export const logOutThunkCreator = () => async dispatch => {
    try {
        dispatch(setIsFetchingLogInLogOutActionCreator(true));
        const response = await logOutDAL();

        if (response.data.resultCode === 0) {
            dispatch(logOutActionCreator());
            dispatch(setIsFetchingLogInLogOutActionCreator(false));
        };
    } catch (error) {
        dispatch(setIsOpeningModalActionCreator(true));
    };
};


export const getCaptchaThunkCreator = () => async dispatch => {
    try {
        dispatch(setIsFetchingLogInLogOutActionCreator(true));

        const response = await getCaptchaDAL();

        dispatch(setUrlCaptchaActionCreator(response.data.url));
        dispatch(setIsFetchingLogInLogOutActionCreator(false));
    } catch (error) {
        dispatch(setIsOpeningModalActionCreator(true));
    };
};