import React, {Suspense, useEffect} from "react";
import {HeaderContainer} from "./Header/HeaderContainer";
import {SideBar} from "./SideBar/SideBar";
import {AppContant} from "./AppContant";
const ModalWindowContainer = React.lazy(() => import("./ModalWindow/ModalWindowContainer"));

const App = ({isAuth, isFetching, getMyProfile}) => {
    useEffect(() => {
        getMyProfile();
    }, []);

    return (
        <>
            <HeaderContainer/>
            <div className={"wrapper"}>
                <div style={styles.contentWrap}>
                    <SideBar/>
                    <AppContant isFetching={isFetching} isAuth={isAuth}/>
                </div>
            </div>
            <Suspense fallback={null}><ModalWindowContainer/></Suspense>
        </>
    );
};

export default App;

const styles = {
    contentWrap: {
        display: "grid",
        gridTemplateColumns: "200px 1fr",
        gridGap: "0 50px"
    }
};

