import {
    changePhotoOnProfileDAL,
    changeProfileInfoDAL,
    changeProfileStatusDAL,
    getProfileDAL,
    getStatusUserDAL
} from "../../api/api";
import {setIsOpeningModalActionCreator} from "./modalReducer";

const getProfile            = "profile/GET PROFILE";
const setProfileStatus      = "profile/CHANGE PROFILE STATUS";
const setIsFetching         = "profile/SET IS FETCHING";

const profileInit = {
    profile: null,
    status: null,
    isFetching: false
};

export const profileReducer = (state = profileInit, action) => {
    const newState = {...state};

    switch (action.type) {
        case getProfile:
            newState.profile  = {...action.profile};
            return newState;
        case setProfileStatus:
            newState.status = action.status;
            return newState;
        case setIsFetching:
            newState.isFetching = action.isFetching;
            return newState;
        default: return state;
    };
};

const getProfileActionCreator = profile => {
    return {type: getProfile, profile};
};

const setStatusActionCreator = status => {
    return {type: setProfileStatus, status};
};

export const setIsFetchingActionCreator = isFetching => {
    return {type: setIsFetching, isFetching};
};

export const getProfileThunkCreator = userId => async dispatch => {
    try {
        dispatch(setIsFetchingActionCreator(true));

        const response = await getProfileDAL(userId);

        dispatch(getProfileActionCreator(response.data));
        dispatch(setIsFetchingActionCreator(false));
    } catch (error) {
        dispatch(setIsOpeningModalActionCreator(true));
    };
};

export const changeProfileInfoThunkCreator = (profile, userId) => async dispatch => {
    profile.userId = userId;
    profile.contacts = {
        vk: profile.vk,
        github: profile.github
    };

    delete profile.vk;
    delete profile.github;

    try {
        dispatch(setIsFetchingActionCreator(true));

        const response = await changeProfileInfoDAL(profile);

        if (response.data.resultCode === 0) {
            await dispatch(getProfileThunkCreator(userId));
            dispatch(setIsFetchingActionCreator(false));
        };
    } catch (error) {
        dispatch(setIsOpeningModalActionCreator(true));
    };
};

export const getProfileStatusThunkCreator = userId => async dispatch => {
    try {
        const response = await getStatusUserDAL(userId);
        dispatch(setStatusActionCreator(response.data));
    } catch (error) {
        dispatch(setIsOpeningModalActionCreator(true));
    };
};

export const changeProfileStatusThunkCreator = formData => async dispatch => {
    try {
        dispatch(setIsFetchingActionCreator(true));
        const response = await changeProfileStatusDAL(formData);

        if (response.data.resultCode === 0) {
            dispatch(setStatusActionCreator(formData.status));
            dispatch(setIsFetchingActionCreator(false));
        };
    } catch (error) {
        dispatch(setIsOpeningModalActionCreator(true));
    };
};

export const changePhotoOnProfileThunkCreator = (image, userId) => async dispatch => {
    try {
        dispatch(setIsFetchingActionCreator(true));
        const response = await changePhotoOnProfileDAL(image);

        if (response.data.resultCode === 0) {
            await dispatch(getProfileThunkCreator(userId));
            dispatch(setIsFetchingActionCreator(false));
        };
    } catch (error) {
        dispatch(setIsOpeningModalActionCreator(true));
    };
};