import {Button} from "antd";
import React from "react";

export const LoginBtn = ({isNotBotInProcessing, getCaptcha}) => {
    if (isNotBotInProcessing) {
        return (
            <>
                <Button type={"primary"} htmlType={"button"} onClick={getCaptcha} style={styles.btn}>Refresh</Button>
                <Button type={"primary"} htmlType={"submit"} style={styles.btn}>Log in</Button>
            </>
        );
    };

    return <Button type={"primary"} htmlType={"button"} onClick={getCaptcha} style={styles.btn}>I'm not bot</Button>;
};

const styles = {
    btn: {
        margin: "20px"
    }
};